.header{
  background-color: #282c34;
  min-height: 10vh;
  display: flex;
  text-align: center;
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  padding: 16px 24px;
}

.main-title{
  font-size: 24px;
  color: white;
}

.icon-back{
  width: auto;
  font-size: 10px;
  background-color: azure;
  border-radius: 5px;
  padding: 5px;
}

@media only screen and (min-width: 1024px ) {
  .main-title{
    font-size: 40px;
  }
}
