@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
  font-family: 'Roboto', sans-serif;
}
.back-tres{
  background-color: #00B18F;
}

*{
  margin: 0;
}

button{
  -moz-appearance: none;
       appearance: none;
  -webkit-appearance: none;
  padding: 0;
  border: none;
  background: none;
}
button:focus, input:focus{
  outline: none;
}
h1, h2, h3, h4, h5, p {
  margin: 0;
}
a:hover{
  outline: 0;
  text-decoration: none;
}

.back-uno{
    background-color: #f3f3f3;
    border: 3px solid #00B18F;
    border-radius: 20px;
    margin: 5px;
    text-align: center;
}
h3{
    margin: 15px;
    color: #CC0001;
}

.bodrow{
    display: flex;
    flex-wrap: wrap;
}

.img-menu{
    width: 80%;
    border-radius: 5px;
    margin-bottom: 15px;
}
p{
    font-weight: 600;
    text-align: left;
    text-align: initial;
}


.container-products{
  width: 80%;
  margin: 12px auto;
  display: flex;
  flex-flow: column;
}

@media only screen and (min-width: 1024px ) {
  .container-products{
    margin: 24px auto;
  }
}

.product{
  border-radius: 5px;
  margin: 4px auto;
  font-size: 16px;
  width: 100%;
  padding: 10px 16px;
  color: black;
  font-family: inherit;
  border: 1px solid #282c34;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.product:hover{
  box-shadow: 0px 4px 10px rgba(20, 43, 127, 0.25);
}

.product-description:first-child {
  width: 74%;
}
@media only screen and (min-width: 1024px ) {
  .product{
    margin: 8px auto;
  }
}

.input-client{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
  box-sizing: border-box;
}

.input-client input{
  border-radius: 8px;
  padding: 12px;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border: 1px solid #FFBE00;
  width: 100%;
  box-sizing: border-box;
  margin: 8px 0;
}

.error-input{
  color: red;
  font-size: 16px;
}

.input-client input:focus{
  outline: none;
}
.input-client input:hover{
  box-shadow: 0px 4px 10px rgba(20, 43, 127, 0.25);
}

.head{
    border-bottom-style: double;
    font-weight: 1000;
}

.sign{
    font-size: 8px;
    color: white;
    height: 18px;
    width: 18px;
    background-color: #282c34;
    padding: 5px;
    border-radius: 3px;
    margin: 0 5px 0  5px;
    border-style: none;
}

i{
    margin: 0px;
}
.total-price{
    margin: 12px 0;
    font-weight: 600;
    border-radius: 6px;
}

.total-price span{
  font-size: 24px;
}

.prod{
    margin-top: 10px;
}

.section-client-name{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  margin-top: 12px;
}

.section-client-name h5{
  font-size: 16px;
}

.empty-box{
  padding: 64px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.empty-box p{
  font-size: 24px;
  margin: 16px;
}

.empty-box img{
  width: 320px;
}

@media only screen and (min-width: 1024px ) {
  .section-client-name{
    flex-direction: row;
    align-items: center;
  }
  .input-client{
    width: 50%;
  }
}

.btn-primary{
  margin: 16px;
  background-color: #282c34;
  color: white;
  border-radius: 5px;
  padding: 12px;
  outline: none;
  text-transform: uppercase;
  font-size: 16px;
}

.btn-primary:hover {
  background-color: #FFBE00;
}

.btn-secondary{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 200px;
  height: 60px;
  background-color: #FFBE00;
  margin: 24px;
  border-radius: 15px;
  outline: none;
  color: #282c34;
}

.btn-secondary:hover{
  background-color: #00B18F;
}

.btn-secondary > a {
  color: #282c34;
  text-decoration: none;
}

.btn-primary:focus, .btn-secondary:focus {
  outline: none;
}

@media only screen and (min-width: 1024px ) {

}

.product-selected{
  border-bottom: 1px solid #282c34;
  padding: 16px 0;
  margin: 0;
}

.product-selected-name{
  font-size: 20px;
}

.product-selected-quantity{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 24px 0;
}

.product-selected-button{
    border-radius: 4px;
    width: 20px;
    height: 20px;
    box-sizing: border-box;
    margin: 0 12px;
    cursor: pointer;
}

@media only screen and (min-width: 1024px ) {
  .product-selected-quantity{
    flex-direction: row;
    align-items: center;
  }
  .product-selected{
    margin: 32px 0;
  }
}

.main-container{
  display: flex;
  max-width: 95%;
  margin: 0 auto;
  flex-direction: column;
}

.section-container{
  width: 100%;
  display: flex;
  flex-direction: column;
}

.center-container{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

@media only screen and (min-width: 1024px ) {
  .main-container {
    width: 100%;
    max-width: 1184px;
    flex-direction: row;
  }
  .section-container{
    flex: 1 1;
  }
}

.header{
  background-color: #282c34;
  min-height: 10vh;
  display: flex;
  text-align: center;
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  padding: 16px 24px;
}

.main-title{
  font-size: 24px;
  color: white;
}

.icon-back{
  width: auto;
  font-size: 10px;
  background-color: azure;
  border-radius: 5px;
  padding: 5px;
}

@media only screen and (min-width: 1024px ) {
  .main-title{
    font-size: 40px;
  }
}

.pedido{
    border-style: groove;
    border-radius: 10px;
    width: 100%;
    text-align: -webkit-center;
    padding: 12px;
    box-sizing: border-box;
    margin: 16px 0;
    position: relative;
}
.pedidos-fire{
    margin: 10px 0;
    font-size: 18px;
}
.title{
    font-size: 20px;
}
.hora-pedido{
    font-size: 15px;
}
.head-pedidos{
    border-bottom: groove;
}
hr{
    border-top: 2px solid #FFBE00;
}

.container-pedidos{
  display: flex;
  flex-flow: wrap;
}

.container-pedidos-prod{
  display: flex;
  justify-content: space-between;
  margin-top: 8px;
}


@media only screen and (min-width: 1024px ) {
  .pedido{
    width: 284px;
  }
  .container-pedidos{
    justify-content: space-between;
  }
  .pedidos-fire{
    min-height: 180px;
  }
}

.inicio{
    background-image: url(https://user-images.githubusercontent.com/45070947/56369372-d5ca5b80-61be-11e9-9f0c-e5d9b0e37d08.png);
    background-attachment: fixed;
    background-size:cover;
    height: calc(100vh - 140px);
    display: flex;
}

a{
    color: #282c34;
    font-weight: 800;
    font-size: 20px;
}

